/* 기본 폼 양식 */
.default_form {
  margin: auto;
  width: 600px;
  background: grey;
  text-align: center;
  font-size: 18px;
}

.default_form > form > ul {
  width: 70%;
}
.default_form > form > ul > li {
  list-style-type: none;
  width: 100%;
  padding: 20px;
}

.default_form > form > ul > li > input {
  float: right;
  height: 30px;
}

.default_form > form > ul > li > select {
  float: right;
  height: 30px;
  width: 177px;
}

/* content 작성칸 크기 */
.create_board {
  width: 80%;
  height: 400px;
  margin: auto;
  margin-bottom: 20px;
  resize: none;
  display: block;
}
